.App-header {
    height: calc(4vh - 3px);
    width: 100%;
    background: #252934;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 3px solid #04c2c9;
    z-index: 100;
}

.App-header.Fixed {
    position: fixed;
    top: 0;
}

.App-header a {
    color: white;
}

.App-header a:hover, a.Description-Projects-Link:hover, .Description-Text a:hover {
    color: #e31b6d;
}

.App-header span {
    margin: 0 30px;
    cursor: pointer;
}

#name {
    font-weight: bold;
    color: #04c2c9;
}

#profession {
    font-weight: bold;
    color: #e31b6d;
}

.Description-About-Link, .Description-Projects-Link {
    font-weight: bold;
    color: white;
}

.Description-About-Link:hover {
    color: #04c2c9;
}

.Description-Text {
    font-size: 20px;
    margin: auto;
}

@media only screen and (max-width: 48em) {
    .Description-Text {
        padding-top: 5vh;
        text-align: justify;
    }
}

.Description-Text a {
    color: #e31b6d;
}

#home {
    min-height: 96vh;
    font-size: 30px;
    background: #252934;
    color: white;
    display: flex;
    flex-direction: column;
}

#self-description-1 {
    padding: 3vh 10%;
    flex: 1;
}

#tech-images {
    padding: 3vh 10%;
    flex: 1;
}

#tech-images img {
    object-fit: scale-down;
}

.Tech-Image-Wrapper {
    display: flex;
    justify-content: center;
    height: 100px;
    /* Enforce large width to prepare the grid when images are hidden (no content = no width).
    If no width, the grid will display and correct itself progressively which isn't great to look at.
    The width will then be corrected by max-width from flexbox grid. */
    width: 1000px;
}

.Section-Title {
    font-size: 30px;
    font-weight: bold;
    padding: 10vh 0 0 0;
    text-align: center;
}

.Section-Title-Bar {
    height: 4px;
    width: 75px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 5vh;
}

.Description-Portrait-Wrapper {
    text-align: center;
}

#skills {
    padding-bottom: 15vh;
}

.Top-Skills {
    font-weight: bold;
    font-size: 22px;
    padding-bottom: 3vh;
}

.Top-Skills-List div {
    padding-bottom: 8px;
}

.Hard-Skills, .Soft-Skills {
    padding-top: 10vh;
}

.Hard-Skills {
    color: #e31b6d;
}

.Soft-Skills {
    color: #04c2c9;
}

@media only screen and (min-width: 64em) {
    .HideIfMdMin {
        display: none;
    }
}

.Skill-Rating {
    width: 100px;
}

.Top-Skills-List div.Soft-Skill-Row {
    padding-bottom: 0;
    margin-bottom: -4.5px;
}

.Skill-Rating-Wrapper {
    text-align: center;
}

.Soft-Skill-Description {
    font-style: italic;
    font-size: 14px;
}

#projects {
    background: #252934;
    color: white;
    padding-bottom: 5vh;
}

.Projects-Disclaimer {
    padding-bottom: 7vh;
    font-weight: normal;
    font-size: 17px;
    text-align: center;
}

.Projects-Showcases div {
    padding-bottom: 5vh;
}

.Showcase-Image-Wrapper {
    text-align: center;
}

.Showcase-Image {
    max-width: fit-content;
    max-width: -webkit-fill-available;
    max-width: -moz-available;
}

.Showcase-Description {
    margin: auto;
    font-size: 20px;
}

.Showcase-Description :first-child {
    padding-bottom: 4px;
}

.Showcase-Description div {
    padding-bottom: 10px;
}

.Showcase-Description .Project-Title {
    color: #04c2c9;
    font-weight: bold;
    font-size: 28px;
}

.Showcase-Description .Project-Description {
    font-style: italic;
}

#contact {
    text-align: center;
    padding-bottom: 10vh;
}

.Contact-Text {
    font-size: 20px;
    padding-bottom: 6vh;
}

.Contact-Chevron {
    fill: #e31b6d;
    transform: scale(2);
    position: relative;
    top: 2vh;
}

@media only screen and (max-width: 32em) {
    .HideIfXsMax {
        display: none;
    }
}